<template>
    <div class="row mb-3">
        <div class="col">
            <div class="row">
                <div class="col align-self-start">
                    <h5>{{id}}</h5>
                </div>
                <div class="col-1 align-self-end">
                    <h5><i v-bind:class=get_expand_icon @click="toggle_expand()" style="cursor: pointer;"></i></h5>
                </div>
            </div>
            <hr />
            <div v-bind:class=get_expand_class>
                <img :src="img_src(image)" class="img-fluid" v-for="image in reversed" :key="image" />
            </div>
            <hr />
        </div>
    </div>
</template>

<script setup>
import { computed, ref } from "vue";
//import { useVSUIStore } from '@/stores/vsui_store.js'
//const store = useVSUIStore();
//const socket = store.socket;
const props = defineProps({
    id: String,
    data: Object
});
const expanded = ref(false)
function toggle_expand() {
    expanded.value = !expanded.value
}
const get_expand_class = computed(() => {
    if (expanded.value) {
        return ""
    } else {
        return "gallery"
    }
})
const get_expand_icon = computed(() => {
    if (expanded.value) {
        return "bi bi-chevron-up"
    } else {
        return "bi bi-chevron-down"
    }
})
const reversed = computed(() => {
    var images = props.data;
    return images.reverse();
});
//const empty = computed(() => {
//    if (length(props.data) > 0) {
//        return false
//   } else {
//        return true
//    }
//})
function img_src(bsfimage) {
    return "data:image/jpeg;base64, " + bsfimage
}
//socket.on('image_push', (push_data) => {
//data.value = JSON.parse(push_data.data);
//})
</script>

